import React, { useEffect } from "react";
import { useSelector, shallowEqual, useDispatch } from "react-redux";
import Grid from "@material-ui/core/Grid";
import { toggleWishListAction } from "../../../../redux/actions/wishListActions.js";

import ItemCard from "../ItemCard/BDItemCard.jsx";

import classes from "./Styles/BDCategoryItems.module.css";

export default function CategoryItems({ isMobileState }) {
  const dispatch = useDispatch();
  const categoryItems = useSelector(
    state => state.categoryReducer.categoryItems,
    shallowEqual
  );

  const wishListState = useSelector(
    state => state.wishListReducer.wishlist,
    shallowEqual
  );

  const itemsAreBeingFetchedState = useSelector(
    state => state.facetReducer.itemsAreBeingFetched,
    shallowEqual
  );

  const toggleWish = (e, id, title, desc, currency_sign, image, price, url) => {
    e.preventDefault();
    dispatch(
      toggleWishListAction(
        id,
        title,
        desc,
        currency_sign,
        image,
        price,
        url,
        wishListState
      )
    );
  };

  const images = Array(
    "https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/images/AINXFRPMS2.jpg",
    "https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/images/AINBVIUDS2.jpg"
  );

  useEffect(() => {
    const img = new Image();
    img.src = images[1];
  }, []);

  const switchImageOnEnter = e => {
    console.log("MOUSE ENTER");
    e.target.style.backgroundImage = `url(${images[1]})`;
  };
  const switchImageOnLeave = e => {
    console.log("MOUSE LEAVE");
    e.target.style.backgroundImage = `url(${images[0]})`;
  };

  if (itemsAreBeingFetchedState) {
    return renderPlaceholderCategoryItems();
  } else {
    return renderCategoryItems(
      categoryItems,
      toggleWish,
      wishListState,
      isMobileState,
      images,
      switchImageOnEnter,
      switchImageOnLeave
    );
  }
}

const renderCategoryItems = (
  categoryItems,
  toggleWish,
  wishListState,
  isMobileState,
  images,
  switchImageOnEnter,
  switchImageOnLeave
) => {
  return window.location.pathname.includes("collections/") ? (
    <div id="categoryId">
      {categoryItems.map(item => {
        if (item.price.type != "empty") {
          return (
            <div className={classes.itemCardCollections}>
              <ItemCard
                itemCard={item}
                key={item.id}
                toggleWish={toggleWish}
                wishListState={wishListState}
              />
            </div>
          );
        }
      })}
    </div>
  ) : (
    <div id="categoryId" className={classes.categoryItemsContainer}>
      {categoryItems.map(item => {
        if (item.price.type != "empty") {
          return (
            <div className={classes.itemCardContainer}>
              <ItemCard
                itemCard={item}
                key={item.id}
                toggleWish={toggleWish}
                wishListState={wishListState}
              />
            </div>
          );
        }
      })}
    </div>
  );
};

export const renderPlaceholderCategoryItems = () => {
  return (
    <Grid container className="item-card-container">
      {Array(8)
        .fill(0, 0, 8)
        .map(_ => (
          <Grid item className="item-card-item" xs={12} sm={6} md={4} lg={3}>
            <div
              className="placeholder-item-card-wrapper"
              style={{ boxShadow: "0px 0px 1px 0px #c8c8c8" }}
            >
              <div
                style={{
                  backgroundColor: "#fff",
                  height: "260px"
                }}
              ></div>
              <div>
                <div
                  style={{
                    backgroundColor: "#f5f5f5",
                    height: "40px"
                  }}
                ></div>
              </div>
            </div>
          </Grid>
        ))}
    </Grid>
  );
};
