import React, {
  useState,
  useContext,
  useRef,
  useLayoutEffect,
  useEffect,
} from "react"
import { useSelector, useDispatch, shallowEqual } from "react-redux"

import Grid from "@material-ui/core/Grid"
import Footer from "../AC-Footer/BDFooter.jsx"
import "../../assets/css/menu.css"
import "../../assets/css/minified.css"
import DesignerHeader from "../AC-CategoryPage/components/CategoryHeader/BDDesignerHeader"
import CollectionsHeader from "../AC-CategoryPage/components/CategoryHeader/BDCollectionsHeader"
import CategoryItems from "../AC-CategoryPage/components/CategoryItems/BDCategoryItems.jsx"
import Facets from "../AC-Facets/BDFacets.jsx"
import Pagination from "../AC-Pagination/BDPagination.jsx"
import NumberOfItems from "../AC-Pagination/NumberofItems.jsx"
import Loading from "../AC-Loading/Loading.jsx"
import FacetBreadcrumb from "../AC-Breadcrumb/BDFacetBreadcrumb.jsx"
import Async from "react-code-splitting"
import SortBy from "../AC-SortBy/SortBy.jsx"
import Facets_Mobile from "../AC-Facets/Facets_Mobile.jsx"
import { renderPlaceholderCategoryItems } from "../AC-CategoryPage/components/CategoryItems/CategoryItems.jsx"
import NoItems from "../AC-CategoryPage/components/NoItems/NoItems"
import classes from "./styles/BDCategory.module.css"

import {
  dispatchScroolPage,
  nextPage,
} from "../../redux/actions/paginationActions.js"
import { I18nContext } from "../../i18n"
import { fetchCategoryFromDirectUrl } from "../../redux/actions/categoryActions.js"
import { useLocation } from "@reach/router"

const CategoryHeader = () => (
  <Async
    load={import(
      "../AC-CategoryPage/components/CategoryHeader/BDCategoryHeader.jsx"
    )}
  />
)

const SubCategoryMenu = () => (
  <Async
    load={import(
      "../AC-CategoryPage/components/SubCategoryMenu/SubCategoryMenu.jsx"
    )}
  />
)

function BDCategory({ categoryName }) {
  const location = useLocation()
  const uglybuttemp = useSelector(
    state => state.categoryReducer.parents,
    shallowEqual
  )

  useEffect(() => {
    if (uglybuttemp.length > 0) {
      if (!uglybuttemp[0][2].includes(categoryName)) {
        dispatch(fetchCategoryFromDirectUrl())
      }
    }
  }, [categoryName, uglybuttemp])

  const { translate } = useContext(I18nContext)
  const [filterButtonClicked, setFilterButtonClicked] = useState(false)

  const dispatch = useDispatch()

  const loading = useSelector(
    state => state.categoryReducer.loading,
    shallowEqual
  )

  const isMobileState = useSelector(
    state => state.mainReducer.isMobile,
    shallowEqual
  )

  const loadingBottom = useSelector(
    state => state.categoryReducer.loadingBottom,
    shallowEqual
  )

  const [canFetchNextPage, setCanFetchNextPage] = useState(true)

  let pathName = location.pathname
  console.info("categoryStyleName", pathName)
  let renderCategoryStyleHeader = false

  if (pathName.includes("designer")) {
    renderCategoryStyleHeader = true
  }

  const renderHeader = () => {
    if (window.location.pathname.includes("collections/"))
      return <CollectionsHeader />
    else if (window.location.pathname.includes("designer"))
      return <DesignerHeader />
    else return <CategoryHeader />
  }

  const pagesState = useSelector(
    state => state.categoryReducer.pages,
    shallowEqual
  )

  const noItemFoundState = useSelector(
    state => state.categoryReducer.noItemFound,
    shallowEqual
  )

  const currentPageState = useSelector(
    state => state.categoryReducer.currentPage,
    shallowEqual
  )

  const scroolPageState = useSelector(
    state => state.categoryReducer.scroolPage,
    shallowEqual
  )

  const cidState = useSelector(
    state => state.categoryReducer.cidN,
    shallowEqual
  )
  console.error("nextPageLink", cidState)

  const handleNextPage = page => {
    if (scroolPageState < pagesState.length || page != 0) {
      dispatch(dispatchScroolPage(page))
      dispatch(nextPage(page, cidState, true))
    } else {
      console.error("End of the pagea")
    }
  }

  const handleFilterButtonClicked = () => {
    document.getElementById("root").setAttribute("aria-hidden", true)
    setFilterButtonClicked(true)
  }

  const handleFacetContentCloseIconClicked = () => {
    document.getElementById("root").setAttribute("aria-hidden", false)
    setFilterButtonClicked(false)
  }

  const renderFacets = (
    <React.Fragment>
      <button
        className={classes.mobileFilterBtn}
        onClick={() => handleFilterButtonClicked()}
      >
        {translate("mobile_facets.filterButtonText")}
        <i className="material-icons">filter_list</i>
      </button>
      <Facets_Mobile
        filterButtonClicked={filterButtonClicked}
        handleFacetContentCloseIconClicked={handleFacetContentCloseIconClicked}
      />
    </React.Fragment>
  )

  const renderMobileFacets = <Facets />

  const myRef = useRef(null)

  useLayoutEffect(() => {
    if (myRef.current) console.error("MYREF MYREF", myRef.current.offsetTop)
    if (!loadingBottom && scroolPageState < 4) {
      let handleOnScroll = () => {
        if (!loading) {
          let scrollTop =
            (document.documentElement && document.documentElement.scrollTop) ||
            document.body.scrollTop
          let scrollHeight = myRef.current.offsetTop
          let clientHeight =
            document.documentElement.clientHeight || window.innerHeight
          let scrolledToBottom =
            Math.ceil(scrollTop + clientHeight) >= scrollHeight

          if (scrolledToBottom) {
            handleNextPage(scroolPageState + 1)
          }
        }
      }

      window.addEventListener("scroll", handleOnScroll)

      // returned function will be called on component unmount
      return () => {
        window.removeEventListener("scroll", handleOnScroll)
      }
    }
  })

  const renderCollectionPlaceHolders = () => {
    return (
      <React.Fragment>
        <Grid container>
          <Grid container className="item-card-container">
            <Grid item className="item-card-item" xs={12} sm={6} md={4} lg={4}>
              <div
                className="placeholder-item-card-wrapper"
                style={{
                  boxShadow: "0px 0px 1px 0px #c8c8c8",
                  marginLeft: "10vw",
                }}
              >
                <div
                  style={{
                    backgroundColor: "#fff",
                    height: "50vh",
                    width: "35vw",
                  }}
                ></div>
                <div>
                  <div
                    style={{
                      backgroundColor: "#f5f5f5",
                    }}
                  ></div>
                </div>
              </div>
            </Grid>
            <Grid item className="item-card-item" xs={12} sm={6} md={4} lg={4}>
              <div
                className="placeholder-item-card-wrapper"
                style={{
                  boxShadow: "0px 0px 1px 0px #c8c8c8",
                  marginLeft: "15vw",
                }}
              >
                <div
                  style={{
                    backgroundColor: "#fff",
                    height: "40vh",
                    width: "17vw",
                  }}
                ></div>
                <div>
                  <div
                    style={{
                      backgroundColor: "#f5f5f5",
                    }}
                  ></div>
                </div>
              </div>
            </Grid>
            <Grid item className="item-card-item" xs={12} sm={6} md={4} lg={4}>
              <div
                className="placeholder-item-card-wrapper"
                style={{
                  boxShadow: "0px 0px 1px 0px #c8c8c8",
                  marginLeft: "2vw",
                }}
              >
                <div
                  style={{
                    backgroundColor: "#fff",
                    height: "40vh",
                    width: "17vw",
                  }}
                ></div>
                <div>
                  <div
                    style={{
                      backgroundColor: "#f5f5f5",
                    }}
                  ></div>
                </div>
              </div>
            </Grid>
          </Grid>
        </Grid>
      </React.Fragment>
    )
  }

  if (noItemFoundState) {
    return <NoItems />
  }

  if (loading) {
    return (
      <div id="bd">
        <div className="main">
          {renderHeader()}
          <Loading />

          {window.location.pathname.includes("collections/") ? (
            renderCollectionPlaceHolders()
          ) : (
            <div
              className={classes.wrapper}
              style={{ gridTemplateColumns: "unset" }}
            >
              <div className="category-container">
                <Grid container justify="space-between" alignItems="center">
                  <Grid
                    item
                    xs={6}
                    sm={4}
                    md={4}
                    lg={3}
                    className="grid-facets-container"
                  >
                    {/*<SubCategoryMenu />*/}
                    {!isMobileState ? renderMobileFacets : renderFacets}
                  </Grid>
                  <Grid
                    item
                    xs={6}
                    sm={8}
                    md={8}
                    lg={9}
                    className="grid-category-items-container"
                  >
                    <Grid container justify="space-around" alignItems="center">
                      <div style={{ margin: "0 1%" }}>
                        <FacetBreadcrumb />
                      </div>
                      <Grid
                        className="pagination-container"
                        item
                        xs={12}
                        style={{
                          backgroundColor: "#f5f5f5",
                          padding: "10px",
                          margin: "0 1%",
                        }}
                      >
                        <Grid
                          container
                          justify="space-around"
                          alignItems="center"
                        >
                          <Grid item xs={6} sm={6} md={3}>
                            <NumberOfItems />
                          </Grid>
                          <Grid item xs={6} sm={6} md={3}>
                            <SortBy />
                          </Grid>
                          <Grid item xs={12} sm={12} md={6}>
                            <Pagination />
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid item xs={12}>
                        {renderPlaceholderCategoryItems()}
                      </Grid>
                    </Grid>
                  </Grid>
                </Grid>
              </div>
            </div>
          )}
        </div>
      </div>
    )
  } else {
    return window.location.pathname.includes("collections/") ? (
      // Collections layout
      <div id="bd">
        <div className={classes.collectionsWrapper}>
          {renderHeader()}
          <CategoryItems />
          {loadingBottom ? <Loading /> : null}
        </div>

        <Footer />
      </div>
    ) : (
      // Regular Layout
      <div id="bd">
        <div className="main">
          {renderHeader()}
          <div className={classes.wrapper}>
            <div>{!isMobileState ? renderMobileFacets : null}</div>
            <div>
              <div className={classes.facetBreadcrumbWrapper}>
                <FacetBreadcrumb />
              </div>
              {isMobileState ? renderFacets : null}
              <div className={classes.subWrapper}>
                <div className={classes.numOfItemsWrapper}>
                  <NumberOfItems />
                </div>
                <div className={classes.sortByPagination}>
                  <div>
                    <SortBy />
                  </div>
                  <div>
                    <Pagination />
                  </div>
                </div>
              </div>

              <CategoryItems />
              {loadingBottom ? <Loading /> : null}
              <div ref={myRef} />
              {!loadingBottom && scroolPageState >= 4 ? (
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <button
                    style={{
                      backgroundColor: "#222e50",
                      borderRadius: "0",
                      boxShadow: "none",
                      fontWeight: "700",
                      textTransform: "uppercase",
                      letterSpacing: "2px",
                      lineHeight: "1.42857143",
                      padding: "6px 15px",
                      fontSize: "16px",
                      color: "white",
                    }}
                    onClick={() => handleNextPage(scroolPageState + 1)}
                  >
                    More Items
                  </button>
                </div>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default BDCategory
