import React, { useState, useEffect } from "react"
import { Link } from "gatsby"
import { useSelector, shallowEqual } from "react-redux"
import { PREVIEW } from "../../../../project-config"
import htmldecoder from "../../../../functions/htmldecoder"
import { useLocation, navigate } from "@reach/router"

import classes from "./Styles/BDItemCard.module.css"

import { I18nContext } from "../../../../i18n"

const ItemCard = props => {
  const location = useLocation()

  const { langCode } = React.useContext(I18nContext)

  const {
    id,
    title,
    code,
    desc,
    currency_sign,
    image,
    itemLargeImage,
    price,
    url,
    properties,
  } = props.itemCard

  console.log("PROPS ITEM CARD", props)

  const isMobileState = useSelector(
    state => state.mainReducer.isMobile,
    shallowEqual
  )

  const [favouriteState, setFavouriteState] = useState("favorite_border")

  let imageUrl = "https://bdadmin3qa.avetti.ca/preview/" + image

  useEffect(() => {
    if (properties.Sys_Num_Images !== "") {
      const numImgs = properties.Sys_Num_Images
      if (parseInt(numImgs) > 1) {
        const img = new Image()
        img.src =
          "https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/images/" +
          code +
          "-2.jpg"
      }
    }
  }, [])

  const switchImageOnEnter = e => {
    e.target.style.backgroundImage = `url(https://bdadmin3qa.avetti.ca/preview/store/20180522154/assets/items/images/${code}-2.jpg)`
    e.target.style.backgroundSize = "cover"
    e.target.style.backgroundPosition = "center"
    e.target.style.width = "100%"
  }
  const switchImageOnLeave = e => {
    e.target.style.backgroundImage = `url(${imageUrl})`
    e.target.style.backgroundSize = "cover"
    e.target.style.backgroundPosition = "center"
    e.target.style.width = "100%"
  }

  let isItemWishlisted = false

  const handleToggleWishlistIcon = (e, id) => {
    e.stopPropagation()
    isItemWishlisted
      ? setFavouriteState("favourite_border")
      : setFavouriteState("favourite")
    props.toggleWish(e, id, title, desc, currency_sign, image, price, url)
  }

  const renderWishlistIcon = () => {
    return (
      <i
        className={
          isItemWishlisted
            ? "no-select material-icons item-card-wishlist-icon active"
            : "no-select material-icons item-card-wishlist-icon"
        }
        id={id}
        onMouseEnter={() => setFavouriteState("favorite")}
        onMouseLeave={() => setFavouriteState("favorite_border")}
        onClick={e => {
          handleToggleWishlistIcon(e, id)
        }}
      >
        {isItemWishlisted || favouriteState === "favorite"
          ? `favorite`
          : `favorite_border`}
      </i>
    )
  }

  const handleClickOnProductCard = (event, url) => {
    console.info("URL", url)
    url = url.replace("product", "")
    console.info("URL", url)
    navigate(langCode !== "en" ? PREVIEW + "/" + langCode + url : PREVIEW + url)
  }

  return (
    <Link to={url}>
      {" "}
      <div className={classes.itemCard}>
        {isItemWishlisted ? renderWishlistIcon() : null}
        {renderWishlistIcon()}
        <div
          className={classes.outer}
          style={{ backgroundImage: `url(${imageUrl})` }}
          onMouseEnter={switchImageOnEnter}
          onMouseLeave={switchImageOnLeave}
        >
          <div className={classes.inner}></div>
        </div>
        <div className={classes.itemText}>
          <div className={classes.itemTitle}>
            <span
              dangerouslySetInnerHTML={{
                __html: htmldecoder(title),
              }}
            ></span>
          </div>
          <div className={classes.price}>
            {price.hasOwnProperty("list_price") ? (
              <div className={classes.listPrice}>${price.list_price}</div>
            ) : null}
            <div style={{ color: "#727A7E" }}>
              ${price.value.integer}.{price.value.decimal}
            </div>
          </div>
          {/* <div className={classes.shortDesc}>
          Custom made midi dress with Chantilly lace
        </div> */}
        </div>
      </div>
    </Link>
  )
}

export default ItemCard
