import React from "react"
import { Link } from "gatsby"
import { PREVIEW } from "../../project-config.js"
import classes from "./Styles/Designers.module.css"
import DesignerContent from "./DesignersContent"

const Designers = () => {
  const designers = DesignerContent()
  console.log("DESIGNERS", designers)

  return (
    <div className={classes.wrapper}>
      <h1 className={classes.header}>DESIGNERS</h1>
      <div className={classes.designers}>
        {designers.map((designer, index) => (
          <Link to={PREVIEW + "/designers/" + designer.path} key={index}>
            <div className={classes.designer}>
              <div className={classes.image}>
                <img src={designer.logo} alt={`${designer.designer} logo`} />
              </div>
              <div className={classes.carousel}>
                <div className={classes.carouselImg}>
                  <img
                    className={classes.imgSides}
                    src={designer.carousel[0]}
                    alt={`${designer.designer} image 1`}
                  />
                </div>
                <div className={classes.carouselImg}>
                  <img
                    className={classes.imgCenter}
                    src={designer.carousel[1]}
                    alt={`${designer.designer} image 2`}
                  />
                </div>
                <div className={classes.carouselImg}>
                  <img
                    className={classes.imgSides}
                    src={designer.carousel[2]}
                    alt={`${designer.designer} image 3`}
                  />
                </div>
              </div>
              <div className={classes.text}>
                See {designer.designer}'s Styles
              </div>
            </div>
          </Link>
        ))}
      </div>
    </div>
  )
}

export default Designers
