import React from "react"
import { Router } from "@reach/router"

import Layout from "../other/layout"
import SEO from "../other/seo"
import Designers from "../other/Designers/Designers"

import Test from "../other/test"
import { PREVIEW } from "../project-config"

const shop = () => {
  console.error("HEY THERE", PREVIEW)
  return (
    <Layout>
      <SEO title="Shop" />
      <Router>
        <Designers path={"/preview/designers"} />
        <Test path={`/preview/shop/:categoryName`} />
      </Router>
    </Layout>
  )
}

export default shop
